import PageButton from '../../components/button.vue';
import EmptyView from '../../components/emptyview.vue';

export default {
	components: {
		PageButton,
		EmptyView
	},
	data() {
		return {
			
		}
	},
	onShow() {
		
	},
	onLoad(options) {
		var url = options.url;
		this.downloadlearn(url);
	},
	onPullDownRefresh() {
	
	},
	methods: {
		downloadlearn:function(url){
			console.log(url);
			uni.downloadFile({
			    url: url, 
			    success: function(res) {
					const filePath = res.tempFilePath
					uni.openDocument({
						filePath:filePath,
						fileType:'doc',
						success: function (res) {
							console.log('打开文档成功')
						}
					})
			    },
				fail:function(res) {
					console.log(res);
				}
			});
		}
	}
}
